* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
body {
  background: #e1e1e1;
  padding: 3% 0;
}
main {
  width: min(1320px, 90%);
  margin: auto;
  display: grid;
  row-gap: 40px;
}
.form-container {
  background: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.form-group {
  margin-bottom: 15px;
}
.form-group label {
  display: block;
  font-weight: 600;
  margin-bottom: 5px;
}
.form-control {
  width: 100%;
  padding: 10px;
  border: 1px solid #a5a5a5;
  border-radius: 4px;
}
.submit-btn {
  background-color: #007bff;
  color: white;
  padding: 10px 15px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}
.submit-btn:hover {
  background-color: #0056b3;
}
.slot {
  background: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.name-class {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 15px;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.justify-between {
  justify-content: space-between;
}
.grid {
  display: grid;
}
.items-center {
  align-items: center;
}
#table {
  margin-top: 20px;
}
.results-title {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 15px;
}
.results-table {
  width: 100%;
  border-collapse: collapse;
}
.results-table th, .results-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}
.results-table th {
  background-color: #f4f4f4;
  font-weight: 600;
}
.results-table tr:nth-child(even) {
  background-color: #f9f9f9;
}
@media (max-width: 410px) {
  main {
    width: 100%;
    padding: 0 5px;
  }

  .form-container {
    padding: 10px;
  }

  .form-group {
    margin-bottom: 10px;
  }

  .form-group label {
    font-size: 14px;
  }

  .form-control {
    padding: 8px;
  }

  .submit-btn {
    font-size: 14px;
    padding: 8px 12px;
  }

  .name-class {
    font-size: 16px;
  }

  .results-title {
    font-size: 18px;
  }

  .results-table th, .results-table td {
    font-size: 12px;
    padding: 6px;
  }
}